.status {
  padding: 5px;
  width: 20px;
  height: 20px;
  position: relative;
  border-radius: 25%;
  &::after {
    content: '';
    width: 10px;
    height: 10px;
    position: absolute;
    border-radius: 50%;
    z-index: 9999;
  }
  &.status-0 {
    background-color: rgba(23, 88, 217, 0.259);
    &::after {
      background-color: rgb(23, 88, 217);
    }
  }
  &.status-1 {
    background-color: rgba(255, 174, 0, 0.259);
    &::after {
      background-color: rgb(255, 174, 0);
    }
  }
  &.status-2 {
    background-color: rgba(25, 138, 23, 0.259);
    &::after {
      background-color: rgb(25, 138, 23);
    }
  }
  &.status-3 {
    background-color: rgba(138, 23, 23, 0.259);
    &::after {
      background-color: rgb(138, 23, 23);
    }
  }
}
